import React from 'react'
import Row from '../shared/layout/Row'
import Spacer from '../shared/layout/Spacer'

const Dashboard = props => {
    return (
        <div className="body-1">
        <h2>Minhas Finanças</h2>
        <img src="./img/finance-art.png" alt="finances"/>
        <h3>Existimos para te ajudar a investir melhor.</h3>
        <span>Somos uma plataforma digital de investimentos, 
            com um único objetivo: entregar a nossos assinantes estratégias 
            tão boas quanto às dos investidores profissionais. E, para isso, 
            contamos com uma das maiores equipes de analistas do Brasil.</span>
        <img src="./img/finance-art-2.png" alt="finances-2"/>
        <h3>Linguagem acessível a todos.</h3>
        <span>Somos contra a visão preconceituosa de que o investidor pessoa física 
            não tem condições de compreender e replicar ideias 'arrojadas' de 
            investimento. Se o investidor pessoa física não consegue compreender e 
            replicar, a culpa é de quem está apresentando a ideia, e não do 
            investidor. Por isso, produzimos nosso conteúdo com linguagem que você 
            consegue entender e colocar em prática.</span>
        <img src="./img/finance-art-3.png" alt="finance-3"/>
        <h3>Temos skin in the game.</h3>
        <span>Nosso modelo de negócios se baseia em um princípio muito simples: 
            indicamos aquilo que, caso fossemos investidores, faríamos para nós mesmos. 
            Nossos assinantes apenas continuam conosco se entregarmos boas recomendações 
            de investimentos. Ou seja, viveremos dia após dia em busca das melhores ideias
            para fazer seu patrimônio crescer.</span>
        </div>
    
    )
}

export default Dashboard