import styled from 'styled-components'

export default styled.header`
   grid-area: header;
   background-color: white;

   display: flex;
   justify-content: center;
   align-items: center;

   h1 {
       font-size: 1.4rem;
       font-weight: 300;
       color: #ddd;
       margin: 0;
   }
   img {
    width: 15vh;
    height: 5vh;
}
      
`