import React from 'react'
import MenuItem from './MenuItem'
import Menu from './Menu'

  

const Nav = (props) => {
    return (
    <nav>
          <Menu>
            <MenuItem to="/" label="Dashboard" icon="home" />
            <MenuItem to="/finances" label="Minhas Finanças" 
            icon="money"/>
          </Menu>
    </nav>
    )
}

export default Nav